@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');
* {
  box-sizing: border-box;
}
body {
  background-color: #9b59b6;
  font-family: 'Lato', sans-serif;
  margin: 0;
  min-height:100vh;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height : 100vh;
  padding: 100px;
}
.display {
  background-color: #fff;
  border-radius: 4px;
  align-items: center;
  justify-content:center;
  display:flex;
  box-shadow:2px 2px 5px rgba(0,0,0,0.4);
  flex-wrap: wrap;
  width: 360px;
  height:450px;
}
.display h1{
  display: block;
  width: 100%;
  align-items:center;
}
.drum-pad {
  background-color:#9b59b6;
  border: 2px solid #333;
  color: #fff;
  box-shadow:2px 2px 5px rgba(0,0,0,0.3);
  cursor: pointer;
  border-radius:4px;
  display:flex;
  align-items: center;
  justify-content:center;
  font-size:60px;
  margin: 10px;
  height:100px;
  width:100px;
}
.drum-pad.active{
  opacity: 0.8;
  transform: scle(0.95);
}